/* ParentheCSS */

/*
NOTE:

Setting `font-size` on `:root` does NOT change the meaning
of `rem` in `@media` queries.
Also, `rem` in `@media` queries is broken in Safari,
but `em` is defined to be the same as `rem` there in non-broken
browsers, so just use `em`.
That makes all of this annoying.

*/

@function Rem($x) {
    @return 1rem * $x;
}
@function Em($x) {
    @return 1em * $x;
}

$rem-scale-factor: 1.25; /* em/rem :: Typically 20px, but accessable. */

@function MediaRem($x) {
    @return Em($rem-scale-factor * $x)
}

$the-max-width: 27.9; /* 27.9rem = c. 558px */
$page-body-min-width: 20;
$lhs-fixed-width: 8;
$gutter-min-width: 1;

$page-container-horizontal-padding: 1;

:root {
    font-size: Rem($rem-scale-factor); /* This is now 1rem. */
    --the-max-width: #{Rem($the-max-width)}; /* 27.9rem = c. 558px */
    --page-body-max-width: calc(var(--the-max-width) * 1.6);
    --page-body-min-width: #{Rem($page-body-min-width)};
    --lhs-max-width: #{Rem($lhs-fixed-width)};
    --lhs-min-width: #{Rem($lhs-fixed-width)};
    --gutter-min-width: #{Rem($gutter-min-width)};
    --gutter-max-width: 1.5rem;
    --outer-horizontal-padding: #{Rem($page-container-horizontal-padding)};
    /* `--current-lhs-offset` helpers */
    --tmp-multicol-max: calc((2 * var(--outer-horizontal-padding))
                             + var(--lhs-max-width)
                             + var(--gutter-max-width)
                             + var(--page-body-max-width));
    --tmp-offsets-both: calc(100vw - var(--tmp-multicol-max));
    --tmp-raw-lhs-offset: calc(var(--tmp-offsets-both) / 2);
    /* `max()` must get units on both args */
    --current-lhs-offset: max(0px, var(--tmp-raw-lhs-offset));
    /* */
    --the-line-height: 1.3;
    --h1-size: 2rem;
    --h2-size: 1.35rem;
    --larger-font-size: 1.15rem;
    --smaller-font-size: 0.9rem;
    --parskip: 0.6rem; /* TODO: might be better as rellative em */
    --tighter-parskip: calc(var(--parskip) / 2); /* for li and dialog elems */
    /* https://material.io/design/color/text-legibility.html */
    --black-text-color: rgba(0, 0, 0, 0.87);
    --maroon-solid: rgba(128, 0, 0, 1.0);
    --maroon-text: rgba(128, 0, 0, 0.87);
    --solid-episcopal-blue: rgba(3, 108, 182, 1.0);
    --half-episcopal-blue: rgba(129, 182, 219, 1.0); /* cf. (3,108,182,0.5) */
    
    /* */
    font-family: "Cormorant", serif;
    line-height: var(--the-line-height);
    text-rendering: optimizeLegibility;
    font-variant-numeric: lining-nums proportional-nums;
    color: var(--black-text-color);
    /*
      NOTE: don't use
      font-variant-ligatures: discretionary-ligatures;
      It does a very strange "st" ligature with Cormorant.
    */
}

body {
    background-color: var(--half-episcopal-blue);
    margin: 0px;
    max-width: 100vw;
}

body > * {
    box-sizing: border-box; /* so 100% widths work right */
    width: 100%;
    box-sizing: border-box;
    padding-right: var(--outer-horizontal-padding);
}

body > header.site-header,
body > footer {
    padding-top: 0.5rem;
    padding-left: calc(var(--outer-horizontal-padding)
                       + var(--current-lhs-offset));
}


a:link, a:visited, a:hover, a:active {
    color: var(--maroon-text);
    font-weight: bold;
}

p,
ul,
ol,
address,
figcaption,
summary {
    margin-top: 0px; /* var(--parskip); */
    margin-bottom: var(--parskip);
}

li {
    margin-top: 0px; /* var(--tighter-parskip); */
    margin-bottom: var(--tighter-parskip);
}

address {
    font-style: normal;
}

span.am-pm-abbrev {
    font-variant-caps: all-small-caps;
}

/* Site header stuff */

header.site-header {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: 1rem;
    grid-template-areas: "logo brent-house"
                         "logo episcopal-at-uchicago"
                         "site-nav site-nav";
    background-color: var(--half-episcopal-blue);
    /* 0.4rem is usually 8 px, like default body margin */
    padding-bottom: 0.5rem;
    
    > {
        h1 {
            grid-area: brent-house;
            margin-top: 0px;
            margin-bottom: 0px;
            font-size: var(--h1-size);
            font-weight: bold;
        }

        h1 + p {
            grid-area: episcopal-at-uchicago;
            margin-top: 0px;
            margin-bottom: 0px;
            font-size: var(--larger-font-size);
        }

        .site-logo {
            grid-area: logo;
            align-self: center;

            img {
                max-width: 30vw; /* calc(var(--the-max-width) / 3); */
                max-height: 5rem;
            }
        }

        nav.site-nav {
            grid-area: site-nav;

            > ul {
                list-style: none;
                padding: 0px;
                margin: 0px;
                max-width: 100%;
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                align-items: stretch;
                justify-content: space-evenly;

                > li {
                    display: block;
                    /* flex: 1; */
                    margin: 0px 1rem;
                    
                    > {
                        a:link, a:visited {
                            display: block;
                            box-sizing: border-box;
                            height: 100%;
                            width: 8.5rem;
                            text-align: center;
                            color: white;
                            background-color: var(--solid-episcopal-blue);
                            border: 0.1rem solid black;
                            border-radius: calc(2 * var(--tighter-parskip));
                            padding: var(--tighter-parskip);
                            font-family: "Fira Sans", sans;
                            font-weight: normal;
                            text-decoration: none;
                        }
                        a:hover, a:active {
                            
                        }
                    }
                }
            }
        }
    }
}

/*  */

.page-container {
    background-color: #ffffff;
    padding-top: 0.5rem;
    padding-bottom: 1rem; /* in addition to parskip from from final child */
    padding-left: var(--outer-horizontal-padding);
    display: grid;
    grid-template-areas: ". .   . header ."
                         ". lhs . content .";
    grid-template-columns: 1fr
                               minmax(var(--lhs-min-width),
                                      var(--lhs-max-width))
                                 minmax(var(--gutter-min-width),
                                        var(--gutter-max-width))
                                 minmax(var(--page-body-min-width),
                                        var(--page-body-max-width))
                                 1fr;
    grid-gap: 0px;

    > {
        header {
            grid-area: header;
            max-width: var(--page-body-max-width);
        }

        aside {
            grid-area: lhs;
            max-width: var(--lhs-max-width);
            text-align: center;
        }

        .page-body {
            grid-area: content;
            max-width: var(--page-body-max-width);
        }
    }
}

.floater {
    clear: both;
}

@mixin floaterAsFloat() {
    .floater {
        float: right;
        max-width: 45%;
        margin: 0px 0px 2rem 1rem;
        > img {
            width: calc(0.7 * var(--the-max-width));
            max-width: 100%;
        }
    }
}
@include floaterAsFloat();

@mixin floaterAsBlock($overhead) {
    @media (max-width: MediaRem((0.914 * $the-max-width) + $overhead)) {
        .floater {
            float: none;
            max-width: 100%;
            margin: 0px 0px var(--parskip) 0px;
            
            > img {
                width: 100%;
            }
        }
    }
}

$page-container-overhead: (2 * $page-container-horizontal-padding);
$multicol-min-overhead: $page-container-overhead
                            + $lhs-fixed-width
                            + $gutter-min-width;


@include floaterAsBlock($overhead: $multicol-min-overhead);


$multicol-min-width: $multicol-min-overhead + $page-body-min-width;

@media (max-width: MediaRem($multicol-min-width)) {
    @include floaterAsFloat();
    
    .page-container {
        grid-template-areas: "lhs" "header" "content";
        grid-template-columns: minmax(auto,calc(100vw - 2rem));
    }
}

@include floaterAsBlock($overhead: $page-container-overhead);


/* */

body > footer {
    background-color: var(--half-episcopal-blue);
    display: flow-root;
}

/* */

h2,
h3,
p,
ul,
ol {
    max-width: var(--the-max-width);
}

ul,
ol {
    box-sizing: border-box;
}

h2 {
    font-size: var(--h2-size);
    margin-bottom: var(--parskip);
    margin-top: calc(var(--parskip) * 3.5);
    font-family: "Fira Sans", sans;
    font-weight: normal;
}

h2:first-child,
h3:first-child {
    margin-top: 0px;
}

h3 {
    font-size: var(--larger-font-size);
    margin-bottom: var(--parskip);
    margin-top: calc(var(--parskip) * 2);
    font-family: "Fira Sans", sans;
    font-weight: 300;
}

/*  */

div.pandemic-update {
    box-sizing: border-box;
    --pandemic-update-gap: 1.25rem;
    padding: var(--pandemic-update-gap);
    margin: 0px
                var(--pandemic-update-gap)
                var(--parskip)
                var(--pandemic-update-gap);
    max-width: calc(var(--the-max-width) - (2 * var(--pandemic-update-gap)));
    border: 0.1rem solid var(--maroon-solid);
    border-radius: 1rem;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: var(--pandemic-update-gap);
    grid-template-areas: "asclepius content";
    align-items: center;

    > {

        img {
            grid-area: asclepius;
            height: 5rem;
        }

        div {
            grid-area: content;

            > {
                p:first-child {
                    margin-top: 0px;
                    font-family: "Fira Sans", sans;
                    font-weight: 300;
                    color: var(--maroon-text);
                }

                :last-child {
                    margin-bottom: 0px;
                }
            }
        }
    }
}
